.PopupForm {
  position: fixed;
  top: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  visibility: collapse;
  opacity: 0;
  transition: all 0.5s ease;
  display: flex;
  z-index: 2;

  &__container {
    background-color: white;
    min-width: 200px;
    min-height: 200px;
    border-radius: 25px;
    position: relative;
  }

  &_is-open {
    visibility: visible;
    opacity: 1;
  }

  &__close {
    top: 0px;
    left: 0px;    
    height: 18px;
    width: 18px;
    background-image: url("../../../images/close.svg");
    background-size: auto;
    border: none;
    transition: 0.3s;
    position: absolute;
    top: -18px;    
    cursor: pointer;
    left: 201px;
  }

  &__close:hover {
    opacity: 0.8;
  }
}
